.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  /* position: absolute; */
  /* height: 100%; */
  color: white;
  height: auto;
  background-color: #130f40;
  background-color: #000000;
  background-image: linear-gradient(147deg, #000000 0%, #04619f 74%);
  margin-top: 40px;
}

.footer_company {
  margin-top: 10px;
  text-align: center;
}

.footer_company h2 {
  /* text-decoration: underline; */
  font-family: "Montserrat", sans-serif;
}

.footer_policy {
  text-align: center;
}

.footer_contactus {
  margin-top: 10px;
  text-align: center;
}

.footer_policy p {
  margin-bottom: 2px;
}

.footer_policy h2 {
  font-family: "Montserrat", sans-serif;
  margin-top: 10px;
  margin-bottom: 10px;
}

.footer_contactus p {
  margin-top: 20px;
}

.footer_contactus h2 {
  font-family: "Montserrat", sans-serif;
}
.footer_contactus img {
  margin-top: 6px;
}

.footer a {
  color: #d9d9d9;
  font-size: 20px;
}

.copyright {
  margin: auto;
  text-align: center;
  font-family: "Arvo", serif;
  font-size: 15px;
}

.footer_company .icons img {
  height: 75px;
}
#myBtn {
  /* display: none; */
  position: fixed;
  bottom: 20px;
  right: 40px;
  z-index: 99;
  border: none;

  background-color: #21b6a8;
  padding: 10px;
  border-radius: 10px;
}

/* #myBtn:hover {
  background-color: grey;
} */

@media (max-width: 575.98px) {
  .copyright {
    margin: auto;
  }
  #myBtn {
    /* display: none; */
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    border: none;
    font-size: 2rem;
    background-color: #21b6a8;
    padding: 10px;
    border-radius: 10px;
  }
}
