@import url("https://fonts.googleapis.com/css2?family=Arvo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400&display=swap");

body {
  background-color: #f1f2f5;
}
.card_ourservices .card {
  padding: 20px;
  background-color: #fafafa;
}
.card_ourservices {
  margin: 50px;
}
.text_our_services {
  margin-top: 40px;
  font-family: "Montserrat", sans-serif;
}
.diagnostic_text h5 {
  font-size: 1.5rem;
}
.card_ourservices .card {
  margin-bottom: 20px;

  box-shadow: 2px 5px 10px rgb(151, 147, 147);
}

.card_ourservices h5,
p {
  font-family: "Arvo", serif;
}

.card_ourservices p {
  text-align: justify;
}

.card_ourservices p {
  font-size: 15px;
}

.diagnostic-card img {
  align-items: center;
}
.diagnosticimg {
  width: 200px;
  height: 200px;
}
@keyframes bounce {
  0%,
  20%,
  60%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  80% {
    transform: translateY(-20ox);
  }
}

@media (max-width: 575.98px) {
  .card_ourservices {
    margin: auto;
  }
  .card_ourservices .card .diagnosticimg {
    margin-left: 75px;
  }
  .card_ourservices .diagnostic_text {
    margin-top: 2px !important;
  }
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .card_ourservices {
    margin-left: 0%;
  }
}
