.navbar {
  background: linear-gradient(
    275deg,
    rgb(5, 68, 94) 10%,
    rgba(24, 154, 180) 60%,
    rgb(117, 230, 218) 100%
  );
  transition: all ease 10s;
}
.navbar {
  box-shadow: 0px 0.5px 0.5px 0.1px #75e6da;
}
.navbar-nav {
  margin-left: auto;
}

.navbar-nav .nav-item a {
  padding: 10px 20px !important;
  transition: all ease 0.5s;
}

.logo {
  height: 51px;
  padding: 0px;
  margin-left: 10vh;
}

.help_logo {
  margin-top: 50px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

.reach_us {
  border: none !important;
  margin-right: 100px;
}

.reach_us:hover {
  background-color: whitesmoke;
  color: #5cb85c;
}

.reach_us:focus {
  outline: none;
  box-shadow: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
@media (max-width: 575.98px) {
  .navbar-nav {
    margin-left: 1px !important;
  }

  .logo {
    height: 40px;
    padding: 0px;
    margin-left: 4vh;
  }

  .help_logo {
    margin-top: 20px;
  }

  #reach_form {
    margin-top: 10px;
  }
  .reach_us {
    margin-right: 25px;
  }
}

@media (max-width: 767.98px) {
  .navbar-nav {
    margin-left: 1px !important;
  }

  .logo {
    height: 40px;
    padding: 0px;
    margin-left: 4vh;
  }
  .reach_us {
    margin-right: 30px;
  }
}

@media (max-width: 411px) {
  .logo {
    height: 35px;
    padding: 0px;
    margin-left: 0vh;
  }
}

@media (max-width: 1024px) {
  .logo {
    height: 51px;
    padding: 0px;
    margin-left: 1vh;
  }
  .reach_us {
    margin-right: 25px;
  }
}

@media (max-width: 321px) {
  .logo {
    height: 35px;
    padding: 0px;
    margin-left: 0vh;
  }
}
