body {
  background-color: white;
}
.booknow {
  margin-top: 100px;
}

#myform {
  margin-top: 10px;
}

.first {
  background-color: #21b6a8;
}
.med {
  margin-top: 10vh;
}
.booknow .card {
  border-radius: 25px !important;
}

@media (max-width: 575.98px) {
  .booknow {
    margin-top: 100px;
  }
  .med {
    margin-top: 1vh;
  }
  #myform {
    margin-top: 0px;
  }
  .first {
    background-color: transparent !important;
  }
}

@media (max-width: 767.98px) {
  .booknow {
    margin-top: 100px;
  }
  .med {
    margin-top: 1vh;
  }
  #myform {
    margin-top: 0px;
  }
  .first {
    background-color: transparent !important;
  }
}

@media (max-width: 768px) {
  .first {
    background-color: transparent !important;
  }
}
