body {
  background-color: #f1f2f5 !important;
}

.text_about_us {
  margin-top: 40px;
  font-family: "Montserrat", sans-serif;
}

.card_aboutus .card {
  border: none !important;
  margin-bottom: 15px;
  background-color: #f1f2f5;
  transition: 0.3s;
}

.card_aboutus .card:hover {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
  transform: translateY(-20px);
}
.card_aboutus {
  margin-top: 40px;
  margin-bottom: 5px;
}

.card_aboutus p {
  font-family: "Arvo", serif;
  /* font-family: "Crimson Text", serif; */
  /* text-align: justify;
  text-justify: inter-word; */
  display: flex;
  text-align: center;
}

.card_aboutus p {
  font-size: 15px;
}

.card-body img {
  height: 100px;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .card_aboutus p {
    display: flex;
    text-align: center;
  }
}
